import { Component, OnInit } from '@angular/core';
import { DataServiceService } from 'src/app/service/data-service.service';
import { GlobalDataSummary } from 'src/app/models/global-data';
import { DateWiseData } from 'src/app/models/date-wise-data';
import { merge } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-countries',
  templateUrl: './countries.component.html',
  styleUrls: ['./countries.component.css']
})
export class CountriesComponent implements OnInit {
data:GlobalDataSummary[];
countries:string[] =[];
totalConfirmed = 0;
totalActive = 0;
totalDeath = 0;
totalRecovred = 0;
todayConfirmed = 0;
todayDeath = 0;
DatewiseData;
SelectedCountryData: DateWiseData[];
loading= true
datatable = [];
p: number = 1;
chart= {
  LineChart: "LineChart",

  height: 500,
  width:400,
  options: {
    animation:{
      duration: 1000,
      easing:'out',
    },
    is3D : true,
  }
}
  constructor( private dataService: DataServiceService) { 

  }

  ngOnInit(): void {
    merge(
      this.dataService.getdateWiseData().pipe(
        map(result=>{
          this.DatewiseData= result;
        })
      ),
      this.dataService.getGlobalData().pipe( map(result=>{
        this.data = result;
        this.data.forEach(cs=>{
          this.countries.push(cs.country)
        })
      }) 
    )
    ).subscribe(
      {
        complete : ()=>{
          this.UpdateValues('India')
          // this.SelectedCountryData = this.DatewiseData['india']
          // this.initChart()
         this.loading= false;
        }
      }
    )

    
}
UpdateValues(country:string){
  this.data.forEach(cs=>{
    if(cs.country == country){
        this.totalConfirmed = cs.confirmed;
        this.totalActive = cs.active;
        this.totalRecovred = cs.recovered;
        this.totalDeath = cs.deaths;

    }
  })
this.SelectedCountryData =this.DatewiseData[country]
// console.log(this.SelectedCountryData)
this.initChart();
}
 get sortData() {
   return this.SelectedCountryData.sort((a, b) => {
     return <any>new Date(b.date) - <any>new Date(a.date);
   });
 }
initChart(){
  this.datatable = [];
 
  this.SelectedCountryData.forEach(cs=>{
    this.datatable.push([cs.date,cs.cases])
    
  })
}
}
